
.email-form {
    max-width: 400px;
    margin: 30px auto 0;
    padding: 20px;
    background-color: #f8f9fa;
    border: 1px solid #dee2e6;
    border-radius: 8px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.1);
}

.field {
    margin-bottom: 20px;
}

label {
    display: block;
    font-weight: bold;
    margin-bottom: 8px;
}

input[type="text"] {
    width: 100%;
    padding: 12px;
    box-sizing: border-box;
    border: 1px solid #ced4da;
    border-radius: 4px;
    font-size: 16px;
}

input[type="submit"] {
    padding: 12px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

input[type="submit"]:hover {
    background-color: #0056b3;
}

.error-message {
    color: #721c24;
    background-color: #f8d7da;
    border: 1px solid #f5c6cb;
    border-radius: 4px;
    padding: 12px;
    margin-bottom: 20px;
}


.social-links {
    display: flex;
    justify-content: center;
    margin-top: 25px;
}

.social-links a {
    text-decoration: none;
    color: black;
    font-size: 16px;
    margin: 0 10px;
    transition: transform 0.2s ease;
    display: flex;
    align-items: center;
}

.social-links a svg {
    width: 24px;
    height: 24px;
}

.social-links a:hover {
    text-decoration: underline;
    transform: scale(1.1);
}
